@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/lato/Lato-Regular.woff2") format("woff2"), url("../fonts/lato/Lato-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/lato/Lato-Semibold.woff2") format("woff2"), url("../fonts/lato/Lato-Semibold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/lato/Lato-Bold.woff2") format("woff2"), url("../fonts/lato/Lato-Bold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 800;
  src: url("../fonts/lato/Lato-Black.woff2") format("woff2"), url("../fonts/lato/Lato-Black.woff") format("woff");
  font-display: swap;
}
:root {
  --h1: 28px;
  --h2: 20px;
  --h3: 18px;
  --m1: 13px;
  --m2: 13px;
  --base-text: 13px;
  --small_text: 13px;
  --width-base: 320;
  --width-current: 100vw;
  --width-multiplier: var(--width-current) / var(--width-base);
}
@media (min-width: 768px) {
  :root {
    --width-base: 768;
    --h1: 36px;
  }
}
@media (min-width: 1252px) {
  :root {
    --width-base: 1920;
    --h1: calc(48 * var(--width-multiplier));
    --h2: calc(36 * var(--width-multiplier));
    --m1: calc(18 * var(--width-multiplier));
    --m2: calc(15 * var(--width-multiplier));
    --base-text: 16;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

img {
  display: block;
}

.eye-catching {
  font-size: inherit;
  font-weight: inherit;
  color: #3e9cdc;
}

.button {
  display: inline-block;
  background-color: #3e9cdc;
  text-transform: uppercase;
  color: #ffffff;
  text-decoration: none;
  text-align: center;
  border: 1px solid #ffffff;
  border-radius: 4px;
  font-family: "Lato", sans-serif;
  cursor: pointer;
  padding: 15px;
  font-size: var(--small_text);
  transition: background-color 0.2s ease-in-out;
}
.button:hover {
  background-color: #286690;
}
.button--white {
  color: #2a2b30;
  background-color: #ffffff;
  border-color: #6f6d7b;
  transition: all 0.2s ease-in-out;
}
.button--white:hover {
  border-color: #ffffff;
  color: #ffffff;
  opacity: 1;
}

.page {
  height: 100%;
}

.page__body {
  min-height: 100%;
  display: grid;
  margin: 0 auto;
  line-height: 1;
  font-size: var(--base-text);
  grid-template-rows: 1fr min-content;
  font-family: "Lato", "Arial", sans-serif;
}
.page__body--no-js {
  grid-template-rows: min-content 1fr min-content;
  overflow: auto;
}

.container {
  max-width: 1252px;
  margin: 0 auto;
  padding: 0 20px;
}
@media (min-width: 768px) {
  .container {
    padding: 0 30px;
  }
}
@media (min-width: 1252px) {
  .container {
    padding: 0 40px;
  }
}

.main-header {
  position: fixed;
  width: 100%;
  background-color: rgba(16, 16, 29, 0.9);
  color: #ffffff;
  z-index: 3;
}
@media (min-width: 1252px) {
  .main-header {
    padding: 10px 0;
  }
}
.main-header--no-js {
  position: relative;
}
@media (min-width: 768px) {
  .main-header--no-js {
    position: sticky;
    top: 0;
  }
}

@media (min-width: 768px) {
  .main-header__wrapper {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: baseline;
  }
}
@media (min-width: 1252px) {
  .main-header__wrapper {
    position: relative;
  }
}

.main-header__logo-wrapper {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  padding: 20px 0;
}

.main-header__logo {
  margin-right: 12px;
  width: 180px;
  transition: opacity 0.2s ease-in-out;
}
.main-header__logo:hover {
  opacity: 0.8;
}
.main-header__logo:active {
  opacity: 0.6;
}
@media (min-width: 768px) {
  .main-header__logo {
    width: 200px;
    margin-right: 0;
  }
  .main-header__logo:hover {
    opacity: initial;
  }
  .main-header__logo:active {
    opacity: initial;
  }
}
@media (min-width: 1252px) {
  .main-header__logo {
    width: initial;
  }
}

.main-header__logo-image {
  display: block;
  width: 100%;
  height: auto;
}

.main-header__toggle {
  position: relative;
  border: none;
  cursor: pointer;
  background-color: transparent;
  width: 24px;
  height: 18px;
  display: flex;
  align-items: center;
  padding: 0;
  margin-left: auto;
  transition: opacity 0.2s ease-in-out;
}
.main-header__toggle:hover {
  opacity: 0.8;
}
.main-header__toggle:active {
  opacity: 0.6;
}
.main-header__toggle::before, .main-header__toggle::after {
  content: "";
  position: absolute;
  height: 2px;
  width: 100%;
  transition: all 0.2s ease-in-out;
  background-color: #ffffff;
}
.main-header__toggle::before {
  top: 0;
}
.main-header__toggle::after {
  bottom: 0;
}
@media (min-width: 768px) {
  .main-header__toggle {
    display: none;
  }
}
.main-header__toggle--no-js {
  display: none;
}

.main-header__toggle-line {
  display: block;
  background-color: #ffffff;
  width: 24px;
  height: 2px;
  transform: scale(1);
}
@media (min-width: 768px) {
  .main-header__toggle-line {
    display: none;
  }
}

.main-header__toggle.main-header__toggle--active .main-header__toggle-line {
  transform: scale(0);
}

.main-header__toggle.main-header__toggle--active::before {
  top: 50%;
  transform: rotate(-47deg) translate(0, -50%);
}

.main-header__toggle.main-header__toggle--active::after {
  bottom: 50%;
  transform: rotate(47deg) translate(0, 50%);
}

@media (min-width: 768px) {
  .main-nav {
    max-width: calc(100% - 220px);
  }
}
@media (min-width: 1252px) {
  .main-nav {
    max-width: calc(100% - 260px);
  }
}
.main-nav--no-js {
  position: static;
  margin: 0 -20px;
}
.main-nav--closed {
  display: none;
}
@media (min-width: 768px) {
  .main-nav--closed {
    display: block;
  }
}
.main-nav--opened {
  position: absolute;
  z-index: 1;
  right: 0;
  left: 0;
  background-color: rgba(16, 16, 29, 0.9);
}
@media (min-width: 768px) {
  .main-nav--opened {
    position: initial;
    background-color: transparent;
  }
}

.main-nav__list {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  list-style: none;
  padding: 0;
  margin: 0;
}
@media (min-width: 768px) {
  .main-nav__list {
    flex-direction: row;
    justify-content: flex-end;
    margin-right: -20px;
  }
}
@media (min-width: 1252px) {
  .main-nav__list {
    margin-right: -30px;
  }
}

.main-nav__item {
  text-align: center;
  border-bottom: 1px solid #e6e6e6;
}
@media (min-width: 768px) {
  .main-nav__item {
    border: none;
    margin: 0;
  }
}
.main-nav__item:first-child {
  border-top: 1px solid #e6e6e6;
}
@media (min-width: 768px) {
  .main-nav__item:first-child {
    border: none;
  }
}

.main-nav__link {
  padding: 20px;
  display: block;
  text-transform: uppercase;
  text-decoration: none;
  transition: color 0.2s ease-in-out;
  font-size: var(--small_text);
  color: inherit;
}
.main-nav__link:hover {
  color: #43abf0;
}
@media (min-width: 768px) {
  .main-nav__link {
    padding: 20px;
  }
}
@media (min-width: 1252px) {
  .main-nav__link {
    padding: 30px;
  }
}

.hero {
  --padding-top: 42px;
  padding: calc(var(--padding-top) + 40px) 0 40px;
  background: image-set(url("../img/index/hero-back@1x.webp") type("image/webp") 1x, url("../img/index/hero-back@2x.webp") type("image/webp") 2x, url("../img/index/hero-back@1x.jpg") 1x, url("../img/index/hero-back@2x.jpg") 2x);
  background-repeat: no-repeat;
  color: #000000;
  background-position: center;
  background-size: cover;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .hero {
    --padding-top: 52px;
    background-position: center;
    background-size: cover;
    padding: calc(var(--padding-top) + 60px) 0 60px;
  }
}
@media (min-width: 1252px) {
  .hero {
    padding: 203px 0;
  }
}

.hero__wrapper {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}
@media (min-width: 425px) {
  .hero__wrapper {
    max-width: 65%;
  }
}

.hero__title {
  margin: 0 0 25px 0;
  font-size: var(--h1);
  font-weight: 700;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .hero__title {
    margin: 0 0 60px 0;
  }
}
@media (min-width: 1252px) {
  .hero__title {
    margin: 0 0 80px 0;
  }
}

.hero__slogan {
  font-weight: 700;
  font-size: var(--h3);
  margin: 0 0 10px 0;
}

.hero__list {
  margin: 0;
  margin-bottom: 25px;
  padding: 0;
  list-style: none;
  display: flex;
  gap: 5px;
  flex-direction: column;
}
@media (min-width: 768px) {
  .hero__list {
    margin: 0 0 60px 0;
  }
}
@media (min-width: 1252px) {
  .hero__list {
    margin: 0 0 80px 0;
  }
}

.hero__item {
  font-size: var(--h3);
  position: relative;
  padding-left: 26px;
}
.hero__item::before {
  content: "-";
  position: absolute;
  left: 0;
  padding: 0 10px;
}

.hero__item-text {
  font-size: inherit;
  color: inherit;
  margin: 0;
  margin-bottom: 5px;
}
.hero__item-text:last-child {
  margin-bottom: 0;
}

.hero__buttons-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 12px;
}
@media (min-width: 768px) {
  .hero__buttons-wrapper {
    max-width: 400px;
    gap: 30px;
  }
}

.order {
  padding: 40px 0;
  color: #ffffff;
  background: image-set(url("../img/index/map@1x.webp") type("image/webp") 1x, url("../img/index/map@2x.webp") type("image/webp") 2x, url("../img/index/map@1x.jpg") 1x, url("../img/index/map@2x.jpg") 2x), #272733;
  background-size: cover;
  background-position: center;
  background-repeat: repeat-y;
  text-align: center;
}
@media (min-width: 768px) {
  .order {
    padding: 60px 0;
  }
}
@media (min-width: 1252px) {
  .order {
    padding: 120px 0 150px;
  }
}

.order__title {
  margin: 0 0 30px;
  font-size: var(--h2);
  text-transform: uppercase;
  font-weight: 800;
}

.order__description {
  margin: 0 0 25px 0;
  color: #bbbbbb;
  font-size: var(--h3);
}
@media (min-width: 768px) {
  .order__description {
    margin: 0 0 60px 0;
  }
}
@media (min-width: 1252px) {
  .order__description {
    margin: 0 0 80px 0;
  }
}

.order__list {
  display: grid;
  gap: 40px 20px;
  list-style: none;
  margin: 0 0 40px 0;
  padding: 0;
  grid-template-columns: repeat(3, 1fr);
  align-items: start;
}
@media (min-width: 768px) {
  .order__list {
    margin: 0 0 60px 0;
    grid-template-columns: repeat(5, 1fr);
    gap: 30px;
  }
}
@media (min-width: 1252px) {
  .order__list {
    margin: 0 0 80px 0;
  }
}

.order__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px 0;
}
.order__item:first-child .order__item-circle-wrapper::after {
  content: initial;
}
.order__item:nth-child(3n+1) .order__item-circle-wrapper::after {
  content: initial;
}
@media (min-width: 768px) {
  .order__item:nth-child(3n+1) .order__item-circle-wrapper::after {
    content: "";
  }
  .order__item:nth-child(5n+1) .order__item-circle-wrapper::after {
    content: initial;
  }
}

.order__item-circle-wrapper {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: center;
}
.order__item-circle-wrapper::after {
  content: "";
  width: 100%;
  height: 6px;
  position: absolute;
  right: calc(50% + 15px);
  top: 50%;
  transform: translateY(-50%);
  background-image: url("../img/icons/stack.svg#points");
  z-index: 1;
}

.order__item-circle {
  position: relative;
  background-color: #3e9cdc;
  border-radius: 50%;
  width: 80px;
  height: 80px;
  border: 5px solid #286690;
  background-image: url("../img/icons/stack.svg#search");
  background-size: 40px 40px;
  background-repeat: no-repeat;
  background-position: center;
  z-index: 2;
}
.order__item-circle--percent {
  background-image: url("../img/icons/stack.svg#percent");
}
.order__item-circle--file {
  background-image: url("../img/icons/stack.svg#file");
}
.order__item-circle--message {
  background-image: url("../img/icons/stack.svg#message");
}
.order__item-circle--money {
  background-image: url("../img/icons/stack.svg#money");
}
@media (min-width: 768px) {
  .order__item-circle {
    width: 100px;
    height: 100px;
    background-size: 55px 55px;
  }
}
.order__item-description {
  margin: 0;
  font-size: var(--base-text);
}

.main-footer {
  padding: 18px 0;
  background-color: #3d4050;
  color: #ffffff;
}

.main-footer__wrapper {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column-reverse;
  align-items: center;
}
@media (min-width: 768px) {
  .main-footer__wrapper {
    display: grid;
    grid-template-columns: 2fr 3fr 2fr;
    gap: 0 14px;
  }
}

.main-footer__list {
  width: 100%;
  position: relative;
  align-self: center;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0;
  padding: 0;
  list-style: none;
}
.main-footer__list::after {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 0;
  width: 18px;
  height: 1px;
  background-color: #c3c3c3;
}
@media (min-width: 768px) {
  .main-footer__list::after {
    bottom: 50%;
    left: -7px;
    width: 1px;
    height: 18px;
    transform: translateY(50%);
  }
}
@media (min-width: 768px) {
  .main-footer__list {
    gap: 0 14px;
  }
}
@media (min-width: 768px) {
  .main-footer__list--social {
    justify-content: flex-end;
    margin-right: -12px;
  }
}

.main-footer__button,
.main-footer__link {
  display: flex;
  align-items: center;
  gap: 10px;
  background: transparent;
  color: inherit;
  border: none;
  cursor: pointer;
  text-decoration: none;
  font-size: var(--small_text);
  padding: 12px;
  transition: opacity 0.2s ease-in-out;
}
.main-footer__button:hover,
.main-footer__link:hover {
  opacity: 0.7;
}
.main-footer__button:active,
.main-footer__link:active {
  opacity: 0.3;
}

.main-footer__copyright-text {
  text-align: center;
  margin: 12px 0;
}
@media (min-width: 768px) {
  .main-footer__copyright-text {
    text-align: initial;
  }
}

.form__wrapper {
  display: grid;
  gap: 20px;
  grid-auto-flow: dense;
}
@media (min-width: 768px) {
  .form__wrapper {
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;
  }
}

.form__label {
  display: flex;
  min-width: 200px;
}

.form__label-select {
  position: relative;
}
.form__label-select::before {
  content: "";
  position: absolute;
  right: 12px;
  top: 50%;
  transform: translateY(-50%);
  width: 20px;
  height: 20px;
  background-image: url("../img/icons/stack.svg#arrow-input");
  background-size: 20px 20px;
  background-repeat: no-repeat;
  transition: transform 0.2s ease-in-out;
}
.form__label-select--open::before {
  transform: translateY(-50%) rotate(-180deg);
}

.form__input {
  width: 100%;
  padding: 12px 10px;
  border: 1px solid #ffffff;
  border-radius: 3px;
  font-size: var(--m1);
  background: rgba(255, 255, 255, 0.85);
  color: #272733;
  font-family: inherit;
  transition: border 0.2s ease-in-out;
}
.form__input::placeholder {
  font-size: var(--m1);
  color: #272733;
}
.form__input:focus, .form__input:hover {
  border-color: #3e9cdc;
  outline: none;
}

.form__input-select {
  padding-right: 35px;
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  cursor: pointer;
}

.form__button {
  width: 100%;
  font-size: var(--m2);
  font-weight: 600;
}
.form__button--add-file {
  display: flex;
  padding: 12px;
  gap: 10px;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  border: 2px solid #3e9cdc;
}
.form__button--add-file::before {
  content: "";
  width: 20px;
  height: 20px;
  mask-image: url("../img/icons/stack.svg#addfile");
  mask-size: 20px 20px;
  mask-repeat: no-repeat;
  background-color: #272733;
  transition: background-color 0.2s ease-in-out;
}
.form__button--add-file:hover::before {
  background-color: #ffffff;
}
.form__button--submit {
  padding: 17px;
}
@media (min-width: 768px) {
  .form__button--submit {
    grid-column: 1/-1;
    margin: 10px auto 0;
    max-width: calc(33% - 15px);
  }
}

.form__range-wrapper {
  width: 100%;
}
@media (min-width: 768px) {
  .form__range-wrapper {
    grid-column: span 2;
  }
}

.form__range-label {
  margin: 0 0 20px;
  display: flex;
  gap: 24px;
  justify-content: space-between;
  color: #ffffff;
  font-size: var(--m1);
  text-align: initial;
}

.form__range-number {
  min-width: 40px;
  font-size: inherit;
  font-weight: 600;
  text-align: right;
}

.form__range-line {
  position: relative;
  margin: 8px 0;
  width: 100%;
  height: 6px;
  border-radius: 12px;
  border: 1px solid #ffffff;
  background-color: rgba(255, 255, 255, 0.85);
  cursor: pointer;
}
@media (min-width: 768px) {
  .form__range-line {
    margin: 0 4px;
  }
}

.form__range-bar {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
}

.form__range-button {
  position: absolute;
  padding: 7px;
  width: 37px;
  height: 37px;
  cursor: pointer;
  background: transparent;
  border: none;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
}
.form__range-button::before {
  content: "";
  position: absolute;
  width: 23px;
  height: 23px;
  border: 3px solid #286690;
  background-color: #3e9cdc;
  border-radius: 50%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}